function InfoPage(){
	let state;
	const s = {
		tab: 'card-tab__tab',
		tabActive: 'card-tab__tab--active',
		tableInner: 'card-tab__inner',
		opened: 'opened-deal',
		completed: 'completed-deal',
		content: 'js-group-content'
	};

	return {
		switchTab: function(data){
			let {group, elem} = data;
			$(`.${s.content}`).hide();
			$(`.${s.content}[data-group="${group}"]`).show();
			$(`.${s.tab}`).removeClass(s.tabActive);
			$(elem).addClass(s.tabActive);
		},

		actions: function(){
			let self = this;
			$(`.${s.tab}`).on('click', function(){
				let group = $(this).data('group');
				let data = {
					group: group,
					elem: this
				};
				self.switchTab(data);
			});
		},

		initState: function(){
			let elem = $(`.${s.tabActive}`);
			let group = elem.data('group');
			let data = {
				group: group,
				elem: elem
			};
			console.log(data);
			this.switchTab(data);
		},

		init: function(){
			console.log('--- InfoPage - works');
			this.initState();
			this.actions();
		}
	}
}

$(document).ready(function() {
	InfoPage().init()
});



